<template>
    <div class="m-joke-item">
        <div class="u-content" @click="handleContent" :class="mode === 'single' ? 'on' : ''">
            <i class="u-star" :class="joke.star ? 'on' : ''" v-if="joke.star" title="Á≤æÈÄâ">‚òÖ</i>
            <span class="u-sentence" v-html="content"></span>
        </div>
        <div class="u-misc">
            <div class="u-op">
                <!-- ‰ΩúËÄÖ -->
                <span class="u-user">
                    <img width="24" height="24" :src="user_avatar | showAvatar" />
                    <a :href="joke.user_id | authorLink" target="_blank" v-if="joke.user_id">{{ user_name }}</a>
                    <span v-else>{{ joke.author || "ÂåøÂêç" }}</span>
                </span>
                <!-- Â§çÂà∂ -->
                <el-link type="primary" class="u-copy" :disabled="disabled" @click="handleCopy(joke.content)">
                    <i class="el-icon-document-copy"></i> Â§çÂà∂
                </el-link>
                <!-- ÁÇπËµû -->
                <a class="u-like" :class="{ on: isLike }" title="Ëµû" @click="addLike">
                    <i class="like-icon">{{ isLike ? "‚ô•" : "‚ô°" }}</i
                    >Like
                    <span class="like-count" v-if="count">{{ count }}</span>
                </a>
                <!-- ÁºñËæëÔºàÁÆ°ÁêÜ&‰ΩúËÄÖ - ÂçïÈ°µÔºâ -->
                <a
                    v-if="mode === 'single' && (isAuthor || isEditor)"
                    class="u-edit el-link el-link--primary is-underline"
                    :href="editLink('joke', joke.id)"
                    target="_blank"
                >
                    <i class="el-icon-edit-outline"></i> ÁºñËæë
                </a>
            </div>
            <div class="u-other">
                <template v-if="isEditor">
                    <!-- Á≤æÈÄâ -->
                    <span class="u-op-item u-op-star el-link el-link--primary is-underline" @click="handleStar">
                        <i :class="isStar ? 'el-icon-star-off' : 'el-icon-star-on'"></i>
                        {{ isStar ? "ÂèñÊ∂àÁ≤æÈÄâ" : "ËÆæ‰∏∫Á≤æÈÄâ" }}
                    </span>
                    <!-- Âà†Èô§ -->
                    <span class="u-op-item u-op-delete el-link el-link--primary is-underline" @click="handleDelete">
                        <i class="el-icon-delete"></i> Âà†Èô§
                    </span>
                    <!-- ÊâìËµè -->
                    <el-checkbox
                        v-if="mode !== 'single'"
                        :disabled="!joke.user_id || isSelf"
                        v-model="checked"
                        @change="doReward"
                        class="u-op-item u-op-gift"
                        >ÊâìËµè</el-checkbox
                    >
                </template>
                <!-- Êó∂Èó¥ -->
                <span class="u-date">
                    <i class="el-icon-date"></i>&nbsp;
                    <time>{{ joke.created_at | dateFormat }}</time>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import JX3_EMOTION from "@jx3box/jx3box-emotion";
import { dateFormat } from "@/utils/dateFormat";
import { showAvatar, authorLink, editLink } from "@jx3box/jx3box-common/js/utils";
import { starJoke, removeJoke, unstarJoke } from "@/service/joke";
import User from "@jx3box/jx3box-common/js/user";
import { postStat } from "@jx3box/jx3box-common/js/stat";
export default {
    name: "joke_item",
    props: ["joke", "mode", "jokeRewardArr"],
    data() {
        return {
            disabled: false,

            // Âä†Êòü
            isStar: this.joke.star,

            // ÁÇπËµû
            count: 0,
            isLike: false,
            checked: false,

            content: "",
        };
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val));
        },
        showAvatar: function (val) {
            return showAvatar(val);
        },
        authorLink,
    },
    computed: {
        isSelf: function () {
            return this.joke.user_id == User.getInfo().uid;
        },
        isListPage: function () {
            return this.mode !== "single";
        },
        isEditor: function () {
            return User.isEditor();
        },
        user_avatar: function () {
            return this.joke?.user_info?.user_avatar;
        },
        user_name: function () {
            return this.joke?.user_info?.display_name;
        },
        isAuthor: function () {
            const user = User.getInfo();
            return user.uid === this.joke.user_id;
        },
    },
    watch: {
        joke: {
            handler: function (val) {
                this.count = val.count;

                this.parse(val.content);
            },
            deep: true,
            immediate: true,
        },
        jokeRewardArr: {
            handler: function (val) {
                this.checked = !!val?.filter((item) => item.article_id == this.joke.id).length;
            },
            deep: true,
            immediate: true,
        },
    },
    methods: {
        async parse(str) {
            const ins = new JX3_EMOTION(str);
            const result = await ins._renderHTML();
            this.content = result;
        },
        // Â§çÂà∂
        handleCopy(str) {
            this.disabled = true;
            navigator.clipboard.writeText(str).then(() => {
                this.copyLabel = "Â∑≤Â§çÂà∂";

                setTimeout(() => {
                    this.copyLabel = "Â§çÂà∂";
                    this.disabled = false;
                }, 3000);
            });
        },
        // ÁºñËæë
        editLink,
        // ÁÇπËµû
        addLike: function () {
            if (this.isLike) return;

            this.count++;
            if (!this.isLike) {
                postStat("joke", this.joke?.id, "likes");
            }
            this.isLike = true;
        },
        // Á≤æÈÄâ
        handleStar() {
            if (!this.isStar) {
                starJoke(this.joke.id).then(() => {
                    this.$notify({
                        title: "ÊàêÂäü",
                        message: "Âä†Á≤æÊàêÂäü",
                        type: "success",
                    });
                    this.isStar = true;
                    this.joke.star = true;
                    this.$forceUpdate();
                    // this.$emit("update");
                });
            } else {
                this.unStar();
            }
        },
        unStar: function () {
            unstarJoke(this.joke.id).then(() => {
                this.$notify({
                    title: "ÊàêÂäü",
                    message: "ÂèñÊ∂àÂä†Á≤æÊàêÂäü",
                    type: "success",
                });
                this.isStar = false;
                this.joke.star = false;
                this.$forceUpdate();
            });
        },
        // Âà†Èô§
        handleDelete() {
            this.$confirm("Ê≠§Êìç‰ΩúÂ∞Ü‰ºöÂà†Èô§ËØ•Ë°®ÊÉÖÔºåÊòØÂê¶ÁªßÁª≠Ôºü", "ÊèêÁ§∫", {
                confirmButtonText: "Á°ÆÂÆö",
                cancelButtonText: "ÂèñÊ∂à",
                type: "warning",
            }).then(() => {
                removeJoke(this.joke.id).then(() => {
                    this.$notify({
                        title: "ÊàêÂäü",
                        message: "Âà†Èô§ÊàêÂäü",
                        type: "success",
                    });
                    if (this.mode === "single") {
                        this.$router.go(-1);
                    } else {
                        this.$emit("update");
                    }
                });
            });
        },
        handleContent: function () {
            this.$router.push(`/joke/${this.joke.id}`);
        },
        //Á°ÆËÆ§ÊâπÈáèÊâìËµè
        doReward() {
            this.$emit("doReward", this.joke);
        },
    },
};
</script>
